import { Component, OnInit } from '@angular/core';
declare var $:any;

@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})
export class SidebarComponent implements OnInit {

  constructor() { }

  ngOnInit() {

$(' #sidbar .sidebar-ul .sidebar-ul2 li').each(function(){
    $(this) .hover(()=>{
      $(this).css({'background-color' :'rgb(199,21,133)'});
      $(this).find('p').css({'display': 'block'}).stop(true).animate({'left':'-105px'},500)

    },()=>{
      $(this).css({'background-color' :''});
      $(this).find('p').css({'display': 'none'}).stop(true).animate({'left':'-6px'},500)
    })
  })
  }
}



