import { Component, OnInit } from '@angular/core';
declare var $:any;

@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.css']
})
export class NavigationComponent implements OnInit {


  constructor() {  }

  ngOnInit() {
    $(function () {
      /*地区*/
      $('#nav .left').hover(function () {
        $('#diqu').show();
        $('#nav .left .a').addClass('ho')
      },function () {
        $('#diqu').hide();
        $('#nav .left .a').removeClass('ho')
      })

     var this1;
      $('#ul1 a').each(function () {
        $(this).click(function () {
          this1=$(this);
          $('#nav .top .left .a').text(
           this1.text()
          )
        })
      });

      /*请登录*/
      $('#nav .top .r .li1').hover(function () {
        $('#nav .top .r .li1 .div1').show();
        $('#nav .top .r .li1 #a1').addClass('a1')
      },function () {
        $('#nav .top .r .li1 .div1').hide();
        $('#nav .top .r .li1 #a1').removeClass('a1')
      })

      /*更多*/
      $('#nav .bottom #gd').hover(function () {
        $('#genduo').show()
      },function () {
        $('#genduo').hide()
      })

      $('#genduo .imgs div').each(function () {
        $(this).hover(function () {
          $(this).find('i').stop(true).animate(
            {height:'96px',
            opacity:'0.5'
            },500);
          $(this).find('p').stop(true).animate(
            {bottom:'10%'},500)
        },function () {
          $(this).find('i').stop(true).animate(
            {height:'48px',
              opacity:'0.1'
            },500);
          $(this).find('p').stop(true).animate(
            {bottom:'-10%'},500)
        })
      })

      /*jQuery滚动隐藏/显示顶部标题*/

      var winheight = $(document).scrollTop();

      $(window).scroll(function() {
        var docheight = $(document).scrollTop();
        if (docheight > 140){$('#nav .bottom').css({'position':'fixed', 'top': '10px','width':'1500px'})}
        else {$('#nav .bottom').css({'position':'static'})}
      });
    })
  }
}



