import { Component, OnInit } from '@angular/core';
declare var $:any;

@Component({
  selector: 'app-nempeople',
  templateUrl: './nempeople.component.html',
  styleUrls: ['./nempeople.component.css']
})
export class NempeopleComponent implements OnInit {

  constructor() { }

  ngOnInit() {

    $('li img').stop(true).mouseenter(function(){
      $(this).animate({
        'opacity' : 0.7
      },800,function() {
        $(this).animate({
          'opacity': 1
        }, 300)
      })
    })
  }

}



