import { Component, OnInit, Input } from '@angular/core';
import {Daygoodstitle, Daygoods, DaygoodsService} from "../shared/daygoods.service";
declare var $:any;

@Component({
  selector: 'app-daygirl',
  templateUrl: './daygirl.component.html',
  styleUrls: ['./daygirl.component.css']
})

export class DaygirlComponent implements OnInit {

  @Input()
  unijn:number;

  daygoodstitles: Array<Daygoodstitle>;

  daygoods: Array<Daygoods>;

  constructor(private daygoodsService: DaygoodsService) {

  }

  ngOnInit() {

    //var un = this.unijn;

    this.daygoodstitles = this.daygoodsService.getDaygoodstitle();
    this.daygoods = this.daygoodsService.getDaygoods();

    /*与鞋包的间隔*/

    var nub = this.daygoods.length/2,
      nub2=nub*300+50;
    $('#girl').css({'margin-bottom' : nub2 + 'px'});

    $('#div1 .div1 li').eq(2).click(() =>{
      $(document).scrollTop(this.unijn + nub2+10);
    })
  }
}



