import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { NavigationComponent } from './navigation/navigation.component';
import { RoastingComponent } from './roasting/roasting.component';
import { NempeopleComponent } from './nempeople/nempeople.component';
import { DaysaleComponent } from './daysale/daysale.component';
import { DaygirlComponent } from './daygirl/daygirl.component'
import {DaygoodsService} from './shared/daygoods.service';
import { SidebarComponent } from './sidebar/sidebar.component';

@NgModule({
  declarations: [
    AppComponent,
    NavigationComponent,
    RoastingComponent,
    NempeopleComponent,
    DaysaleComponent,
    DaygirlComponent,
    SidebarComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule
  ],
  providers: [DaygoodsService],
  bootstrap: [AppComponent]
})
export class AppModule { }



