import { Component, OnInit } from '@angular/core';
declare var $:any;

@Component({
  selector: 'app-daysale',
  templateUrl: './daysale.component.html',
  styleUrls: ['./daysale.component.css']
})
export class DaysaleComponent implements OnInit {

  private stores: Array<Store>;

   val:boolean = false;

      inpNub:number;

  constructor( ) { }

  ngOnInit() {
    this.stores=[
      new Store(1,'检查一切美好事物','银牌会员尊享本专场商品折上再9折优惠','//d.vpimg1.com/upcb/2017/08/25/135/0828broadcast.jpg','//a.vpimg3.com/upload/brandcool/0/dea6e9c3fcfd4bf79da085d0dbf845ec/10000866/primary.png',['998','//a.vpimg3.com/upload/merchandise/pdc/546/763/461132429829763546/1/BROAD-BDJ4G633P10-5_235x297_90.jpg','899','//a.vpimg3.com/upload/merchandise/pdc/788/230/447058680994230788/1/BROAD-BDJ4G751P10-5_235x297_90.jpg','998','//a.vpimg3.com/upload/merchandise/pdc/592/890/451280805644890592/1/BROAD-BDJ4G636K00-5_235x297_90.jpg']),
      new Store(2,'简约 生活艺术的美','简约 生活艺术的美','//d.vpimg1.com/upcb/2017/08/25/61/1503667425590.jpg','//a.vpimg3.com/upload/brandcool/0/95ec4c446e8e4bc29256d0da4868e9e5/10000957/primary.png',['636','//a.vpimg4.com/upload/merchandise/pdc/471/206/105910991151206471/1/9095270683019-5_235x297_90.jpg','556','//a.vpimg3.com/upload/merchandise/pdc/323/202/105910991151202323/2/E-9095250093019-5_235x297_90.jpg','695','//a.vpimg4.com/upload/merchandise/pdcvis/104135/2017/0620/75/cd03b0f1-089a-40a3-959b-7569b75278bb_t_235x297_90.jpg']),
      new Store(3,'检查一切美好事物','银牌会员尊享本专场商品折上再9折优惠','//d.vpimg1.com/upcb/2017/08/25/104/ck1_xffq.jpg','//a.vpimg3.com/upload/brandcool/0/336e2ffb987a4746ae66626219e7e524/10000484/primary.png',['998','//a.vpimg3.com/upload/merchandise/pdcvis/2017/06/09/42/5a4dd682-a60c-4991-9dc5-31c026b2d1c3_235x297_90.jpg','899','//a.vpimg4.com/upload/merchandise/pdcvis/2017/08/14/114/692cd2c4-8488-43a4-b614-28164a3ba944_235x297_90.jpg','998','//a.vpimg3.com/upload/merchandise/pdcvis/2017/06/08/49/3675e1ea-1489-4405-805b-c98f88b8d1e7_235x297_90.jpg']),
      new Store(4,'简约 生活艺术的美','简约 生活艺术的美','//d.vpimg1.com/upcb/2017/08/25/61/1503667425590.jpg','//a.vpimg3.com/upload/brandcool/0/95ec4c446e8e4bc29256d0da4868e9e5/10000957/primary.png',['636','//a.vpimg4.com/upload/merchandise/pdc/471/206/105910991151206471/1/9095270683019-5_235x297_90.jpg','556','//a.vpimg3.com/upload/merchandise/pdc/323/202/105910991151202323/2/E-9095250093019-5_235x297_90.jpg','695','//a.vpimg4.com/upload/merchandise/pdcvis/104135/2017/0620/75/cd03b0f1-089a-40a3-959b-7569b75278bb_t_235x297_90.jpg']),
      new Store(5,'检查一切美好事物','银牌会员尊享本专场商品折上再9折优惠','//d.vpimg1.com/upcb/2017/08/25/135/0828broadcast.jpg','//a.vpimg3.com/upload/brandcool/0/dea6e9c3fcfd4bf79da085d0dbf845ec/10000866/primary.png',['998','//a.vpimg3.com/upload/merchandise/pdc/546/763/461132429829763546/1/BROAD-BDJ4G633P10-5_235x297_90.jpg','899','//a.vpimg3.com/upload/merchandise/pdc/788/230/447058680994230788/1/BROAD-BDJ4G751P10-5_235x297_90.jpg','998','//a.vpimg3.com/upload/merchandise/pdc/592/890/451280805644890592/1/BROAD-BDJ4G636K00-5_235x297_90.jpg']),

      new Store(6,'简约 生活艺术的美','简约 生活艺术的美','//d.vpimg1.com/upcb/2017/08/25/61/1503667425590.jpg','//a.vpimg3.com/upload/brandcool/0/95ec4c446e8e4bc29256d0da4868e9e5/10000957/primary.png',['636','//a.vpimg4.com/upload/merchandise/pdc/471/206/105910991151206471/1/9095270683019-5_235x297_90.jpg','556','//a.vpimg3.com/upload/merchandise/pdc/323/202/105910991151202323/2/E-9095250093019-5_235x297_90.jpg','695','//a.vpimg4.com/upload/merchandise/pdcvis/104135/2017/0620/75/cd03b0f1-089a-40a3-959b-7569b75278bb_t_235x297_90.jpg']),
      new Store(7,'检查一切美好事物','银牌会员尊享本专场商品折上再9折优惠','//d.vpimg1.com/upcb/2017/08/25/135/0828broadcast.jpg','//a.vpimg3.com/upload/brandcool/0/dea6e9c3fcfd4bf79da085d0dbf845ec/10000866/primary.png',['998','//a.vpimg3.com/upload/merchandise/pdc/546/763/461132429829763546/1/BROAD-BDJ4G633P10-5_235x297_90.jpg','899','//a.vpimg3.com/upload/merchandise/pdc/788/230/447058680994230788/1/BROAD-BDJ4G751P10-5_235x297_90.jpg','998','//a.vpimg3.com/upload/merchandise/pdc/592/890/451280805644890592/1/BROAD-BDJ4G636K00-5_235x297_90.jpg'])

    ]

    /*侧边导航栏*/
    $('#div1 .div1 li').eq(0).addClass('slid-li').end().eq(11).css({'border-bottom':'0 solid rgba(155, 155, 155, 0.38)'});

    $('#div1 .div1 li').click(function(){
      $(this).siblings('li').removeClass('slid-li').css({'border-bottom':'1px solid rgba(155, 155, 155, 0.38)','color':'rgba(0, 0, 0, 0.5)'})
        .end()
        .addClass('slid-li').css({'border-bottom':'0 solid rgba(155, 155, 155, 0.38)','color':'white'})
        .prev().css({'border-bottom':'0 solid rgba(155, 155, 155, 0.38)'});
      $('#div1 .div1 li').eq(11).css({'border-bottom':'0 solid rgba(155, 155, 155, 0.38)'})
    });


    var nub2=this.stores.length;

    /*点击定位*/
    $('#div1 .div1 li').eq(0).click(function(){ $(document).scrollTop(1145) });

    this.inpNub=Math.ceil(nub2/2)*320+1400;

    $('#div1 .div1 li').eq(1).click(function(){ $(document).scrollTop(Math.ceil(nub2/2)*320+1400)});

    $('#div1 .div1 li').hover(function(){
      $(this).css({'color':'rgba(241, 1, 128,1)'}).filter('.slid-li').css({'color':'white'})
    },function(){
      $(this).css({'color':'rgba(0, 0, 0, 0.5)'}).filter('.slid-li').css({'color':'white'})
    });


    var winheight = $(document).scrollTop();
    $(window).scroll(function() {
      var docheight = $(document).scrollTop();

      if (docheight > 1210){$('#div1 .div1').css({'position':'fixed','left':'4%'})}
      else {$('#div1 .div1').css({'position':'absolute', 'left': '-11%'})}
    });


    /*与女装的间隔*/

    var nub3=(nub2-1)/2;

    heightModel1();
    function heightModel1(){
      if( nub3 < 3 ){
        $('#model1').css({'top':-(3-nub3)*320+'px'})
      }else {
        $('#model1').css({'top':(nub3-3)*320+'px'})
      }
    }
  }

  ondo(){
    $('#div1 .div4').hover(function () {
      $(this).find('.div4-3').show();
      $(this).find('.div4-1').css({'opacity':0.5});
    },function () {
      $(this).find('.div4-3').hide();
      $(this).find('.div4-1').css({'opacity':1})
    })
  }
}

export class  Store{
  constructor(
    public id:number,
    public title:string,
    public desc:string,
    public titleimg:string,
    public titlelog:string,
    public product:Array<string>
  ){}
}



