import { Component, OnInit } from '@angular/core';
declare var $:any;

@Component({
  selector: 'app-roasting',
  templateUrl: './roasting.component.html',
  styleUrls: ['./roasting.component.css']
})
export class RoastingComponent implements OnInit {

  constructor() {}

  ngOnInit() {
  var _index=0;
  var timePlay=null;
    var len = $('.div1-1 li').length;

    $('.div1 .ul1 li').eq(0).show().siblings("li").hide();//只显示第一张

    $(".div1-1 li").mouseenter(function(){

      clearInterval(timePlay);
      _index=$(this).index();
      showImg(_index)
      $(".div1 .ul1 li").eq(_index).fadeIn().siblings("p").fadeOut();//淡出

     }).mouseleave(function(){
      autoPlay();
    });


    function showImg(_index) {
      $(".div1 .ul1 li").eq(_index).fadeIn().siblings("li").fadeOut();

  $(".div1-1 li").css("borderBottom","0 solid red")
    .eq(_index).css("borderBottom","3px solid red")
}

    //自动轮播
    function autoPlay() {
      timePlay = setInterval(function () {
        _index++;
        if (_index < len) {

          if (_index == len) {_index = 0;}

          $(".div1 .ul1 li").eq(_index).fadeIn().siblings("li").fadeOut();

          $(".div1-1 li").css("borderBottom","0 solid red")
            .eq(_index).css("borderBottom","3px solid red")
        }
        else {_index = -1;}
      }, 2000);
    };
    autoPlay();//调用和执行
  }}



